.header {
  height: 64px;
  width: 100%;

  background-color: var(--primary-color);
  color: #fff;
  font-weight: 400;
  font-size: 15px;
  padding: 10px 20px;
  position: relative;
  z-index: 999;
}

.flex_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1300px !important;
}

.left_menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.logo {
  position: relative;
  color: #fff;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 0px;
  margin-bottom: 10px;
  cursor: pointer;
}

.logo_u {
  font-size: 33px;
  font-weight: 300;
  -webkit-text-stroke-width: 0.4px;
  -webkit-text-stroke-color: #000;
  margin-right: -0.5px;
}

.logo_clone {
  position: absolute;
  left: 8px;
  bottom: -10px;
  font-size: 13px;
  font-weight: 400;
  -webkit-text-stroke-width: 0.4px;
  -webkit-text-stroke-color: #000;
}

.left_menu > ul > li {
  padding: 9px 12px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.left_menu > ul > li:hover {
  background-color: #f6f6f63e;
}

.left_menu > ul {
  display: flex;
  gap: 14px;
}

.company_li {
  display: flex;
  align-items: center;
  gap: 10px;
}

.company_dropdown {
  background-color: #fff;
  color: #919191;
  font-size: 16px;
  font-weight: normal;
  box-shadow: 3px 4px 21px 5px rgba(0, 0, 0, 0.09);
  border-radius: 8px;
  padding-bottom: 12px;
  position: absolute;
  top: 64px;
}
.company_dropdown > ul > li {
  padding: 10px 50px 10px 15px;
}
.company_dropdown > ul > li:hover {
  color: #000;
  background-color: var(--tertiary-color);
  border-radius: 0;
  width: 100%;
}

/* ----> Right menu */
.right_menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.right_menu > span {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 9px 12px;
  border-radius: 50px;
  margin-right: 18px;
  cursor: pointer;
  transition: all 0.3s;
}

.right_menu > span:hover {
  background-color: #f6f6f63e;
}

.right_menu > button {
  font-size: 14px;
  font-weight: 400;
  padding: 8px 10px;
  margin: 0 2px;
  border-radius: 20px;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.3s;
}

.right_menu > button:hover {
  background-color: var(--tertiary-color);
}

.right_menu > button:first-of-type {
  background-color: #000;
  color: #fff;
}
.right_menu > button:first-of-type:hover {
  background-color: rgb(53, 53, 53);
}

/* ----> Language Modal */
.modal {
  position: absolute;
  width: 100%;
  height: calc(100vh - 64px);
  color: #000;
  background-color: #fff;
  top: 64px;
  left: 0;
  padding: 12vh 18vw;
  animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  z-index: 50;
}

.modal > i {
  /* width: 100%; */
  text-align: center;
  font-size: 33px;
  margin-left: 96%;
  padding: 14px 13px 4px 13px;
  border-radius: 80px;
  cursor: pointer;
}

.modal > i:hover {
  background-color: var(--tertiary-color);
}

.modal > h4 {
  font-size: 33px;
  margin-bottom: 60px;
}

.modal > button {
  font-size: 17px;
  padding: 16px 80px 16px 20px;
  font-weight: 500;
  border-radius: 6px;
  margin-right: 40px;
  background-color: var(--tertiary-color);
}

.modal > button:nth-of-type(2) {
  background-color: transparent;
}

.modal > button:hover {
  background-color: var(--tertiary-color);
}

@keyframes slide-bottom {
  0% {
    transform: translateY(-400px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Products dropdown */
.products_dropdown {
  height: 300px;
  width: 250px;
  overflow-y: scroll;
  font-size: 19px;
  font-weight: 500;
  color: #000;
}
.products_dropdown > ul > li {
  padding: 0 0 0 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}
.products_dropdown > ul > li > span {
  padding: 20px 10px 20px 15px;
  border-bottom: 1px solid rgb(241, 241, 241);
  width: 100%;
}
.products_dropdown > ul > li > svg {
  font-size: 28px;
}
.products_dropdown > ul > li:hover {
  color: #000;
  background-color: var(--tertiary-color);
  border-radius: 0;
  width: 100%;
}

/* ====> RESPONSIVE STYLES */
button.menu_btn {
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

button.menu_btn:hover {
  background-color: #424242;
}

/* ----> Mobile Menu */
.mobile_menu {
  position: absolute;
  width: 100%;
  height: calc(100vh - 64px);
  color: #000;
  background-color: #fff;
  top: 64px;
  left: 0;
  padding: 5vw;
  animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  z-index: 40;
}

.mobile_menu > ul > li {
  font-size: 33px;
  font-weight: 500;
  margin-bottom: 28px;
}

.mobile_menu > span:nth-child(2) {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-size: 18px;
  font-weight: 400;
  background-color: var(--tertiary-color);
  padding: 12px;
  border-radius: 5px;
  margin-top: 16vh;
}

.mobile_menu > span:nth-child(3) {
  display: flex;
  gap: 8px;
  font-weight: 500;
  margin-top: 40px;
}

@media (max-width: 1080px) {
  .modal {
    padding: 2vh 3vw;
  }

  .modal > i {
    display: none;
  }

  .modal > button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 18px;
    text-align: left;
  }

  .company_li {
    justify-content: space-between;
  }

  .products_dropdown {
    height: 100%;
    width: 100%;
    top: 0;
    z-index: 50;
  }
}
